import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        topics: [ //tab
            { name: 'all', desc: '' },
            { name: 'good', desc: '' },
            { name: 'share', desc: '' },
            { name: 'ask', desc: '' },
            { name: 'job', desc: '' },
            { name: 'dev', desc: '' },
        ],
        isShowMoreMenus: false, //
        // moreMenus: {}, // 
        isLogined: true, //
        unLoginedMenus: { //
            login: '',
        },
        loginedMenus: { //
            center: '',
            create: '',
            msg: '',
            logout: '',
        },
        loginname: 'YT-Yanting', // 
        accesstoken: '212ce88b-cae9-411f-9fde-fdefa526d859', // accesstoken
        authorId: '615428befe0c515cd3aec367', // ()id
    },
    getters: {
        moreMenus: function(state) {
            // state storestate
            if (state.isLogined) {
                return state.loginedMenus
            } else {
                return state.unLoginedMenus
            }
        }
    },
    mutations: {
        // mutationsisShowMoreMenus stateisShowMoreMenus  actionsheet()
        mutationsisShowMoreMenus: function(state, payload) {
            state.isShowMoreMenus = payload;
        },
        // 
        mutationIsLogined: function(state) {
            state.isLogined = true;
        },
        // 
        mutationLoginname: function(state, payload) {
            state.loginname = payload;
        },
        // accesstoken
        mutationAccesstoken: function(state, payload) {
            state.accesstoken = payload;
        },
        // ()id
        mutationAuthorId: function(state, payload) {
            state.authorId = payload;
        },
        // 
        mutationLogout: function(state) {
            state.isLogined = false;
            state.loginname = '';
            state.accesstoken = '';
            state.authorId = '';
        }
    },
    actions: {},
})

export default store;


