
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';

export default ({
name:'Edit',
props:['id'],
components:{
Group,XTextarea,mavonEditor,XButton,Toast
},
data:function(){
return {
topic:{
title:'',
content:'',
},
toolbars:{
bold: true, // 
italic: true, // 
header: true, // 






ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 






/* 1.3.5 */


trash: true, // 
save: false, // eventssave
/* 1.4.2 */
navigation: true, // 
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
subfield: true, // 
preview: true 
}
}
},
beforeRouteEnter:function(to,from,next){
next(function(vm){
vm.$http.get('/topic/'+to.params.id).then(function(res){

vm.topic=res.data.data;
})
})
},
beforeRouteUpdate:function(to,from,next){
var that = this
this.$http.get('/topic/'+to.params.id).then(function(res){

that.topic=res.data.data;
});
next();
},
methods:{

edittopic:function(){
var that = this;
this.$http.post('/topics/update',{
accesstoken:this.$store.state.accesstoken,
topic_id:this.id,
title:this.topic.title,
content:this.topic.content,
tab:'dev',//
}).then(function(res){



that.$router.back();
})
}
}
})



