
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
import axios from 'axios';
export default {
data:function(){
return {
title:'',
content:'',
toastText:'',
isShowToast:false,//
toolbars:{
bold: true, // 
italic: true, // 
header: true, // 






ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 






/* 1.3.5 */


trash: true, // 
save: false, // eventssave
/* 1.4.2 */
navigation: true, // 
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
subfield: true, // 
preview: true 
}
}
},
components:{
Group,XTextarea,mavonEditor,XButton,Toast
},
methods:{
addtopic:function(){
var that = this;
this.$http.post('/topics',{
accesstoken:this.$store.state.accesstoken,
title:this.title,
content:this.content,
tab:'dev',//
}).then(function(res){

that.$router.replace('/topiclist/dev');
}).catch(function(error){

that.isShowToast=true;
that.toastText = '';
})
},

uploadimage:function(pos,file){
console.log('')



var formdata = new FormData( );
formdata.append( 'image',file);
var that= this;
axios.post('https://sm.ms/api/v2/upload',formdata,{




headers: {'Content-Type':'multipart/formdata'},//
}).then(function(res){



that.$refs.md.$img2url(pos,res.data.message.url);





})

}
}
}



